.tile-grid {
    display: flex;
    flex-direction: row;
    gap: 1.25em;
    margin: 1.5em 0;
}

@media screen and (max-width: 500px) {
    .tile-grid {
        flex-direction: column;
    }
}

.tile {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .7em;

    color: var(--text-0);
    font-size: 1.1em;
    font-weight: bold;

    background-color: var(--bg-1);
    padding: 1.5em 1em;
    border: none;
    border-radius: var(--radius-0);
    transition: .2s ease;
}

.browser-icon {
    opacity: .8;
}

.tile.selected .browser-icon {
    filter: invert(1);
    opacity: 1;
}

.tile div {
    text-align: left;
}

.tile:hover {
    background-color: #e5e5e5;
}

.detected-text {
    font-size: .9em;
    font-weight: normal;
}

.tile:not(.selected) .detected-text {
    color: green;
}

.tile.selected {
    color: white;
    background-color: green;
}