:root {
    --bg-0: #fff;
    --bg-1: #f2f2f2;
    --bg-2: var(--bg-0);
    --text-0: #333;
    --radius-0: .75em;
    --radius-1: .5em;
    --gap: 4em;
}

body {
    color: var(--text-0);
    background-color: var(--bg-1);
    font-size: 17px;
    line-height: 1.5;
}

@media screen and (max-width: 900px) {
    body {
        font-size: 14px;
    }
}

#root {
    background-color: var(--bg-0);
    max-width: 75ch;
    margin: 0 auto;
    padding: 0 min(5vw, 4em);
}

section {
    padding: 0 0 4em;
}

section > *:last-child {
    margin-bottom: 0;
}

a {
    color: #1f65c3;
    text-decoration: none;
    text-decoration-thickness: 1px;
}

a:hover {
    text-decoration: underline;
}

h2 {
    font-size: 1.75em;
    border-bottom: 1px solid #ccc;
    padding-bottom: .2em;
    margin: 0 0 .8em;
}

code {
    font-size: .9em;
    background-color: var(--bg-1);
    padding: .2em .5em;
    border-radius: .3em;
}

.media {
    display: block;
    max-width: 100%;
    height: auto;
    margin: 1em 0 1.5em;
    border: 1px solid #ddd;
    border-radius: var(--radius-0);
    box-shadow: 0 3px 24px -6px rgba(0, 0, 0, 0.25);
}

video.media {
    width: 91%;
    margin: auto;
}

footer {
    color: #777;
    font-size: .85em;
    text-align: center;
    padding-bottom: 4em;
}